import React, { Component } from 'react'

export default class ContactForm extends Component {
  render() {
    return (
      <div className="contact-form">
        <h3>Need help with design?</h3>
        <p>Contact our graphic designer service directly</p>
        <a href="mailto:office@kiteprint.org.uk?Subject=Inquiry"><h4>design@kiteprint.org.uk</h4></a>
        <br />
        <div className="input-group mb-3">
          <select defaultValue="1" className="custom-select" id="inputGroupSelect01">
            <option value="1">Potrzebujesz pomocy w projekcie wizytwki</option>
            <option value="2">Potrzebujesz pomocy w projekcie ulotki</option>
            <option value="3">Potrzebujesz pomocy w projekcie dokumentw firmowych</option>
            <option value="4">Potrzebujesz pomocy w projekcie kalendarza</option>
            <option value="5">Potrzebujesz pomocy w projekcie dokumentw firmowych</option>
            <option value="6">Potrzebujesz pomocy w projekcie inne</option>
            <option value="7">A moe Brand creation</option>
          </select>
        </div>
        <div className="input-group mb-3">
          <textarea className="form-control" aria-label="Message" placeholder="Message"></textarea>
        </div>
        <div className="input-group mb-3">
          <input type="text" className="form-control" aria-label="Full Name" placeholder="Full Name" aria-describedby="basic-addon1" />
        </div>
        <button className="btn btn-block btn-circle custombtn" type="button" >
          Submit
        </button>
      </div>
    )
  }
}



