import React, { Component } from "react";
import Info from "../components/Info";
import pic1 from "../img/lpf/rollip.jpg"
import pic2 from "../img/lpf/car.jpg"
import pic3 from "../img/fancy/billvoard.jpg"
import pic4 from "../img/lpf/Bus-Stop.png"
import pic5 from "../img/lpf/poster.jpg"
import pic6 from "../img/lpf/flag.png"
import pic7 from "../img/lpf/flag2.jpg"
import pic8 from "../img/lpf/flag3.jpg"
import pic9 from "../img/lpf/citylight.jpg"
import pic10 from "../img/lpf/bannerrrr.jpg"
import pic11 from "../img/lpf/stand.jpg"
import pic12 from "../img/lpf/kiteprint.jpg"

export default class LargePrintFormat extends Component {
  constructor(props) {
    super(props);
    this.state = { modal: null };
    this.handleModal = this.handleModal.bind(this);
  }
  handleModal(e) {
    e.preventDefault();
    this.setState({ modal: e.target.style.backgroundImage.split('"')[1] })
  }
  render() {
    return (
      <div className="LargePrintFormat">
        <div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-body" >
                <img className="modal-img" src={this.state.modal} alt={this.state.modal} />
              </div>
            </div>
          </div>
        </div>
        <div className="section-background">
          <h2>LARGE PRINT FORMAT</h2>
        </div>
        <Info current="large-print-format" />
        <div className="section-text">
          <p>
            We aim to support and develop services for business enterprises, projects, personal and private needs. This might be for small businesses, large companies or personal individual occasions and celebrations. Whichever service you are looking for we will tailor and offer solutions to provide for your requirements.
          </p>
        </div>
        <div className="section-text">
          <h2 className="section-header">PROFESSIONAL SERVICES</h2>
          <div className="lpf-grid">
            <div className="lpf-item lpf-professional-services-1" onClick={this.handleModal} style={{ backgroundImage: `url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-2" onClick={this.handleModal} style={{ backgroundImage: `url(${pic2})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-3" onClick={this.handleModal} style={{ backgroundImage: `url(${pic3})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-4" onClick={this.handleModal} style={{ backgroundImage: `url(${pic4})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-5" onClick={this.handleModal} style={{ backgroundImage: `url(${pic5})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-6" onClick={this.handleModal} style={{ backgroundImage: `url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-7" onClick={this.handleModal} style={{ backgroundImage: `url(${pic7})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-8" onClick={this.handleModal} style={{ backgroundImage: `url(${pic8})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-9" onClick={this.handleModal} style={{ backgroundImage: `url(${pic9})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-10" onClick={this.handleModal} style={{ backgroundImage: `url(${pic10})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-11" onClick={this.handleModal} style={{ backgroundImage: `url(${pic11})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
            <div className="lpf-item lpf-professional-services-12" onClick={this.handleModal} style={{ backgroundImage: `url(${pic12})` }} data-toggle="modal" data-target="#exampleModalCenter"></div>
          </div>
        </div>
        <h2 className="section-header">YOUR ORDER</h2>
        <div className="lpf-input">
          <div>
            <h4>PRODUKT</h4>
            <input type="text" className="form-control" placeholder="Your e-mail address" aria-label="email" aria-describedby="basic-addon1" />
          </div>
          <div>
            <h4>TYP</h4>
            <input type="text" className="form-control" placeholder="Enter your name here" aria-describedby="basic-addon1" /></div>
          <div className="file-input">
            <h4>LOGO</h4>
            Nie wybrano pliku
            <label className="btn btn-primary">
              Browse <input type="file" hidden />
            </label>
          </div>
          <div>
            <h4>TEXT</h4>
            <input type="text" className="form-control" placeholder="Add some text here" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>WIDTH</h4>
            <input type="text" className="form-control" placeholder="cm" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>HEIGHT</h4>
            <input type="text" className="form-control" placeholder="cm" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>MATERIAL</h4>
            <input type="text" className="form-control" aria-label="email" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>NAKLAD</h4>
            <input type="text" className="form-control" aria-label="email" aria-describedby="basic-addon1" /></div>
        </div>
        <div className="lpf-submit">
          <input type="text" className="form-control" placeholder="Enter e-mail address" aria-describedby="basic-addon1" />
          <input type="text" className="form-control" placeholder="Your telephone number" aria-describedby="basic-addon1" />
          <button type="button" className="btn btn-primary btn-circle">
            SUBMIT YOUR ORDER
          </button>
        </div>
      </div >
    );
  }
}



