import React, { Component } from "react";

export default class Cookies extends Component {
  render() {
    return (
      <div className="Cookies">
        <div className="container-cookies">
          <div className="cookies-circle blank">!</div>
          <div className="cookies-text">
            <p>
              Nasz strona internetowa uywa plikw cookies(tzw. ciasteczka) w
              celach statystycznych, reklamowych oraz funkcjonalnych. Dziki nim
              moemy indywidualnie dostosowa stron do twoich potrzeb. Kady
              moe zaakceptowac pliki cookies albo ma moliwo wyaczenia ich w
              przegldarce, dziki czemu nie bd zbierane adne informacje.
            </p>
          </div>
        </div>
      </div>
    );
  }
}



