import React, { Component } from "react";
import Info from "../components/Info";

export default class Specialist extends Component {
  render() {
    return (
      <div className="Specialist">
        <div className="section-background">
          <h2>SPECIALIST</h2>
        </div>
        <Info current="specialist" />
        <div className="section-card-grid">
          <div>
            <h3>
              Those who need specialized prints of the highest quality we offer
              a wide range of professional and technical services
            </h3>
            <p>
              We make every order from simple printing, binding or folding to
              highly customizable training materials, architectural designs and
              more.
            </p>
            <ul>
              <li>
                Copying the full documentation of the building during the
                process of its creation and approval of development plans
              </li>
              <li>Building permits</li>
              <li>Tender documentation</li>
              <li>Final construction documentation</li>
              <li>Documentation of as-built projects</li>
              <li>
                CAD drawings created on the basis of programs: AutoCAD,
                Microstation, Allplan in pdf, tiff, jpg
              </li>
              <li>Large Format Scan to B0</li>
              <li>Folding drawings up to A4 / A3</li>
              <li>Picking and finishing works</li>
              <li>Sort the drawings in the correct order</li>
              <li>Binding of documents, binding</li>
              <li>
                Providing the necessary materials (spirals, folders,
                personalized luminaries, etc)
              </li>
              <li>
                Distribution of design documentation to architectural offices,
                engineering firms or directly to construction sites
              </li>
            </ul>
            <p>
              This service is aimed to people who work in technical or technical
              fields only. In each case, please contact us before ordering.
            </p>
          </div>
          <div className="specialist-image" />
        </div>
      </div>
    );
  }
}



