import React, { Component } from "react";
import Info from "../components/Info";
import sizes from "../img/default/sizes.jpg";

export default class Canvas extends Component {
  render() {
    return (
      <div className="Canvas">
        <div className="section-background">
          <h2>PAINTING ON CANVAS</h2>
        </div>
        <Info current="canvas" />
        <div className="section-card-grid">
          <div>
            <h3>Painting on canvas</h3>
            <p>Your memories deserve to be remembered.</p>
            <p>
              Thanks to canvas prints your home will show your dreams, magic
              moments or dreams every day. Create a real talking point at your
              home. Or maybe goal points at office.
            </p>
            <p>
              Or maybe goal points at office. Or just simple gift for someone
              you love.
            </p>
            <p>
              Our Canvas are available in 20 sizes, you can choose wrapped edges
              or a white border, always safe and Easy to install, tiles are hung
              edge to edge, Environmentally friendly ink is used during
              production and the wallpaper material is free from PVC.
            </p>
          </div>
          <div className="canvas-image" />
        </div>
        <div className="canvas-content">
          <p>
            Photo prints: Gallery, Collage or Panoramic Wallpapers: 2 panel set,
            3 panel set or Multi panel canvas Cut stickers, photo-blinds, Images
            from your own picture or images from art Graphics on glass
          </p>
          <img src={sizes} className="canvas-image-sizes" alt="Sizes" />
          <p>Or tell us what size is the best for you.</p>
          <p>There is a huge chance that we can do it for you.</p>
        </div>
      </div>
    );
  }
}



