import React, { Component } from "react";

export default class Info extends Component {
  constructor(props) {
    super(props);
    this.state = { current: props.current }
  }
  render() {
    return (
      <div className="Info">
        <div className="container-info" style={{ textAlign: "left" }}>
          <div className="blank" />
          <div className="info-item">
            <a href="/business-cards" style={{ color: this.state.current === "business-cards" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>BUSINESS CARD</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/calendars" style={{ color: this.state.current === "calendars" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>CALENDARS</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/fancy-papers" style={{ color: this.state.current === "fancy-papers" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>FANCY PAPERS</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/leaflets" style={{ color: this.state.current === "leaflets" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>LEAFLETS FLYERS</b>
            </a>
          </div>
          <div className="info-item" >
            <a href="/printing" style={{ color: this.state.current === "printing" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>PRINTING</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/specialist" style={{ color: this.state.current === "specialist" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>SPECIALIST</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/canvas" style={{ color: this.state.current === "canvas" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>CANVAS</b>
            </a>
          </div>
          <div className="info-item">
            <a href="/large-print-format" style={{ color: this.state.current === "large-print-format" ? "rgb(6, 150, 225)" : "#555" }}>
              <b>LARGE FORMAT PRINT</b>
            </a>
          </div>
          <div className="blank" />
        </div>
      </div >
    );
  }
}



