import React, { Component } from "react";

export default class Services extends Component {
  render() {
    return (
      <div className="Services">
        <div className="services">
          <h2>
            <b>YOUR IDEAS NEED THE BEST PRESENTATION</b>
          </h2>
          <button type="button" className="btn btn-outline-primary btn-circle">
            SEE OUR SERVICES
          </button>
        </div>
      </div>
    );
  }
}



