import React, { Component } from "react";
import carousel1 from "../img/cards/bamboo.jpg";
import carousel2 from "../img/cards/divided.jpg";
import carousel3 from "../img/cards/cork.jpg";

export default class Spotuv extends Component {
  render() {
    return (
      <div className="Spotuv">
        <div className="container-spotuv">
          <div className="spotuv-item">
            <div
              className="carousel slide"
              data-ride="carousel"
              data-interval="3000"
            >
              <div className="carousel-inner">
                <div className="carousel-item active">
                  <img className="d-block w-100" src={carousel1} alt="First slide" />
                </div>
                <div className="carousel-item">
                  <img
                    className="d-block w-100"
                    src={carousel2}
                    alt="Second slide"
                  />
                </div>
                <div className="carousel-item">
                  <img className="d-block w-100" src={carousel3} alt="Third slide" />
                </div>
              </div>
            </div>
          </div>
          <div className="spotuv-item">
            <h2>
              <b>OUR MISSION</b>
            </h2>
            <p>
              Lorem ipsum dolor sit amet consectetur, adipisicing elit. Fugit ea
              asperiores maxime, accusamus voluptatum ex provident tempore cum
              mollitia est tenetur iste amet nam, dolorem blanditiis ipsum
              laborum expedita harum quo repellat. Necessitatibus id, quos saepe
              tenetur ratione quo quidem dolore impedit dolores deleniti
              architecto magni asperiores quisquam ad fugit est autem
              voluptatem, nisi voluptatibus. Eligendi saepe deserunt voluptatem
              amet!!
            </p>
            <a href="/contact">
              <button
                type="button"
                className="btn btn-outline-primary btn-circle btn-spotuv"
              >CONTACT US
            </button>
            </a>
            <div className="arrows">
              <div className="arrow arrow-left" />
              <div className="arrow arrow-right" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}



