import React, { Component } from "react";

export default class Footer extends Component {
  render() {
    return (
      <div className="Footer">
        <footer>
          <div className="footer-item footer-item-logo">
            <div className="footer-logo" />
            <h3>kiteprint</h3>
            <h5>BIURO OBSUGI KLIENTA</h5>
            <p>Od poniedziaku do pitku w godzinach 8:00 - 16:00</p>
            <br />
            <p>tel. 873 939 932</p>
            <a href="mailto:office@kiteprint.org.uk?Subject=Inquiry">
              <button
                type="button"
                className="btn btn-outline-secondary btn-circle"
              >CONTACT US
            </button>
            </a>
          </div>
          <div className="footer-item">
            <h5>ONLINE PRINTING HOUSE</h5>
            <a href="/">
              <p>Regulamin sklepu</p>
            </a>
            <a href="/">
              <p>Oglne warunki sprzeday</p>
            </a>
            <a href="/">
              <p>Polityka prywatnoci i cookies</p>
            </a>
            <a href="/about">
              <p>O nas</p>
            </a>
            <a href="/contact">
              <p>Kontakt</p>
            </a>
            <a href="/">
              <p>Wyceny produktw</p>
            </a>
            <a href="/">
              <p>Pytania i odpowiedzi</p>
            </a>
          </div>
          <div className="footer-item">
            <h5>FOLLOW US</h5>
            <div className="icon-container">
              <a href="/">
                <i className="fab fa-twitter" />
              </a>
              <a href="/">
                <i className="fab fa-pinterest" />
              </a>
              <a href="/">
                <i className="fab fa-linkedin-in" />
              </a>
              <a href="/">
                <i className="fab fa-google-plus-g" />
              </a>
              <a href="/">
                <i className="fab fa-facebook-f" />
              </a>
            </div>
          </div>
        </footer>
      </div>
    );
  }
}



