import React, { Component } from "react";
import Info from "./Info";
import ContactForm from "./ContactForm";

export default class Products extends Component {
  render() {
    return (
      <div className="Products">
        <section className="products">
          <div className="products-background">
            <h2>PRODUCTS AND SERVICES</h2>
          </div>
          <Info />

          <div className="identity">
            <ContactForm />
            <div className="identity-text">
              <h3>YOU NEED GRAPHIC FOR YOUR IDENTITY?</h3>
              <p>
                Lorem ipsum dolor sit amet consectetur, adipisicing elit.
                Laboriosam impedit incidunt laborum fuga! Ea repudiandae
                necessitatibus a? Doloribus eum nisi odit quia. Temporibus
                libero, adipisci officiis aliquam cupiditate animi ullam omnis
                architecto numquam excepturi placeat aperiam culpa quaerat ipsum
                dolor beatae dolorem vitae. Tempore quis quibusdam officiis
                temporibus? Repellat, qui.
              </p>
              <button
                type="button"
                className="btn btn-outline-primary btn-circle btn-read-more"
              >
                CONTACT WITH US
              </button>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



