import React, { Component } from "react";
import Info from "../components/Info";
import ContactForm from "./ContactForm";
import pic1 from "../img/cards/economy.jpg"
import pic2 from "../img/cards/laminated.jpg"
import pic3 from "../img/cards/cork.jpg"
import pic4 from "../img/cards/laminate.jpg"
import pic5 from "../img/cards/pearl-card.jpg"
import pic6 from "../img/cards/rounded.jpg"
import pic7 from "../img/cards/metalic.jpg"
import pic8 from "../img/cards/copper.jpg"
import pic9 from "../img/cards/ecology.jpg"
import pic10 from "../img/cards/Colourful-edges.jpg"
import pic11 from "../img/cards/gold-and-silver.jpg"
import pic12 from "../img/cards/folded.jpg"
import pic13 from "../img/cards/unsual-shapes.jpg"
import pic14 from "../img/cards/rounded.jpg"
import pic15 from "../img/cards/divided.jpg"
import pic16 from "../img/cards/square.jpg"
import pic17 from "../img/cards/cork.jpg"
import pic18 from "../img/cards/bamboo.jpg"
import pic19 from "../img/cards/ecology.jpg"
import pic20 from "../img/cards/pearl-card.jpg"
import pic21 from "../img/cards/spot-uv.jpg"
import pic22 from "../img/cards/perforated.jpg"
import pic23 from "../img/cards/Hot-Stamping.jpg"
import pic24 from "../img/cards/3d-embossing.jpg"
import pic25 from "../img/cards/laminate.jpg"
import pic26 from "../img/cards/elegant.jpg"
import pic27 from "../img/cards/laminate.jpg"
import pic28 from "../img/cards/sparkle.jpg"
import pic29 from "../img/cards/letter.jpg"
import pic30 from "../img/cards/loyalty-card.jpg"
import pic31 from "../img/cards/folded.jpg"

export default class BusinessCards extends Component {
  constructor(props) {
    super(props);
    this.state = { modal: null };
    this.handleModal = this.handleModal.bind(this);
  }
  handleModal(e) {
    e.preventDefault();
    this.setState({ modal: e.target.style.backgroundImage.split('"')[1] });
  }
  render() {
    return (
      <div className="BusinessCards">
        <div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-body" >
                <img className="modal-img" src={this.state.modal} alt={this.state.modal} />
              </div>
            </div>
          </div>
        </div>
        <div className="section-background">
          <h2>BUSINESS CARDS</h2>
        </div>
        <Info current="business-cards" />
        <div className="section-card-grid">
          <div style={{ textAlign: "left" }}>
            <h3>Business Card</h3>
            <p>
              Take Your business to the next level with exclusive and innovative
              features. Thanks to Our impact coloured core business cards You
              can make a great first impression.
            </p>
            <p>
              Our company offers printing many original solutions and materials.
            </p>
            <p>
              They are with the highest quality and always tailored to the needs
              of the customer. Your business card should be unique and designed
              under your needs, so our team is always open and friendly to help
              you with necessary solutions.
            </p>
            <p>
              Is easy  just supply us with logos or graphics you have and we'll
              work together to choose colours and a layout.
            </p>
            <p>We offer a full range of business cards</p>
          </div>
          <ContactForm />
        </div>
        <div className="business-card-product-grid">
          <div className="product card-economy" onClick={this.handleModal} style={{ backgroundImage: `url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>ECONOMY</p>
          </div>
          <div className="product card-laminated" onClick={this.handleModal} style={{ backgroundImage: `url(${pic2})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>LAMINATED</p>
          </div>
          <div className="product card-cork" onClick={this.handleModal} style={{ backgroundImage: `url(${pic3})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>TEXTURED</p>
          </div>
          <div className="product card-colored-letters" onClick={this.handleModal} style={{ backgroundImage: `url(${pic4})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>KASZEROWANE</p>
          </div>
          <div className="product card-pearl" onClick={this.handleModal} style={{ backgroundImage: `url(${pic5})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>SILK</p>
          </div>
          <div className="product card-matt" onClick={this.handleModal} style={{ backgroundImage: `url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>MATT</p>
          </div>
          <div className="product card-metalic" onClick={this.handleModal} style={{ backgroundImage: `url(${pic7})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>METALLIC</p>
          </div>
          <div className="product card-copper" onClick={this.handleModal} style={{ backgroundImage: `url(${pic8})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>COPPER</p>
          </div>
          <div className="product card-ecology" onClick={this.handleModal} style={{ backgroundImage: `url(${pic9})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>ECOLOGY</p>
          </div>
          <div className="product card-colourful-edges" onClick={this.handleModal} style={{ backgroundImage: `url(${pic10})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>COLOURFUL EDGES</p>
          </div>
          <div className="product card-gold-silver" onClick={this.handleModal} style={{ backgroundImage: `url(${pic11})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Gold &amp; Silver</p>
          </div>
          <div className="product card-folded" onClick={this.handleModal} style={{ backgroundImage: `url(${pic12})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>COLOURED LETTERS</p>
          </div>
          <div className="product card-unusual" onClick={this.handleModal} style={{ backgroundImage: `url(${pic13})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>UNUSUAL SHAPES</p>
          </div>
          <div className="product card-matt" onClick={this.handleModal} style={{ backgroundImage: `url(${pic14})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>ROUNDED CORNER</p>
          </div>
          <div className="product card-divided" onClick={this.handleModal} style={{ backgroundImage: `url(${pic15})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>DIVIDED</p>
          </div>
          <div className="product card-square" onClick={this.handleModal} style={{ backgroundImage: `url(${pic16})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>SQUARE</p>
          </div>
          <div className="product card-cork" onClick={this.handleModal} style={{ backgroundImage: `url(${pic17})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>CORK</p>
          </div>
          <div className="product card-bamboo" onClick={this.handleModal} style={{ backgroundImage: `url(${pic18})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>BAMBOO</p>
          </div>
          <div className="product card-ecology" onClick={this.handleModal} style={{ backgroundImage: `url(${pic19})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>WOODEN</p>
          </div>
          <div className="product card-pearl" onClick={this.handleModal} style={{ backgroundImage: `url(${pic20})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>PEARL</p>
          </div>
          <div className="product card-spot-uv" onClick={this.handleModal} style={{ backgroundImage: `url(${pic21})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>SPOT UV</p>
          </div>
          <div className="product card-perforated" onClick={this.handleModal} style={{ backgroundImage: `url(${pic22})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>PERFORATED</p>
          </div>
          <div className="product card-hot-stamping" onClick={this.handleModal} style={{ backgroundImage: `url(${pic23})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>HOT-STAMPING</p>
          </div>
          <div className="product card-3d" onClick={this.handleModal} style={{ backgroundImage: `url(${pic24})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>3D EMBOSSING</p>
          </div>
          <div className="product card-standard" onClick={this.handleModal} style={{ backgroundImage: `url(${pic25})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>LUXURY</p>
          </div>
          <div className="product card-elegant" onClick={this.handleModal} style={{ backgroundImage: `url(${pic26})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>ELEGANT</p>
          </div>
          <div className="product card-colored-letters" onClick={this.handleModal} style={{ backgroundImage: `url(${pic27})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>COLOURS</p>
          </div>
          <div className="product card-sparkle" onClick={this.handleModal} style={{ backgroundImage: `url(${pic28})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>SPARKLE</p>
          </div>
          <div className="product card-letter" onClick={this.handleModal} style={{ backgroundImage: `url(${pic29})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>LETTERPRESS</p>
          </div>
          <div className="product card-loyalty" onClick={this.handleModal} style={{ backgroundImage: `url(${pic30})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>LOYALTY CARDS</p>
          </div>
          <div className="product card-folded" onClick={this.handleModal} style={{ backgroundImage: `url(${pic31})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>FOLDED</p>
          </div>
          <div className="product card-background">
            <p>MANY MORE</p>
          </div>
        </div>
        <h2 className="section-header">YOUR ORDER</h2>
        <div className="lpf-input">
          <div>
            <h4>MAIL</h4>
            <input type="text" className="form-control" placeholder="Company mail" aria-label="email" aria-describedby="basic-addon1" />
          </div>
          <div>
            <h4>TITLE</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>PRODUCT</h4>
            <input type="text" className="form-control" placeholder="Product Type" aria-describedby="basic-addon1" /></div>
          <div className="file-input">
            <h4>MATERIAL</h4>
            <input type="text" className="form-control" placeholder="MATERIAL" aria-describedby="basic-addon1" />
          </div>
          <div className="file-input">
            <h4>GRAPHICS</h4>
            <label className="btn btn-primary">
              FRONT <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              BACK <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              TEXT FRONT <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              TEXT BACK <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              LOGO <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              OTHER <input type="file" hidden />
            </label>
          </div>
          <div>
            <h4>BUSINESS CARD TEXT</h4>
            <input type="text" className="form-control" placeholder="cm" aria-describedby="basic-addon1" /></div>
        </div>
        <div className="lpf-submit">
          <button type="button" className="btn btn-primary btn-circle">
            SUBMIT YOUR ORDER
          </button>
        </div>
        <div className="find-us">
          If you can't find the information you require please do not hesitate to <a href="/contact"><b>contact us</b></a>
          <h4><a href="mailto:office@kiteprint.org.uk?Subject=Inquiry">design@kiteprint.org.uk</a> &nbsp;  &nbsp; 07445679555</h4>
        </div>
      </div>
    );
  }
}



