import React, { Component } from "react";
import Info from "../components/Info";
import pic1 from "../img/leaflets/a4.jpg"
import pic2 from "../img/leaflets/dl.jpg"
import pic3 from "../img/leaflets/dl_trojdzielna.jpg"
import pic4 from "../img/leaflets/dl_.jpg"
import pic5 from "../img/leaflets/aquare.jpg"
import pic6 from "../img/leaflets/material.jpg"
import pic7 from "../img/leaflets/laminated.jpg"
import pic8 from "../img/leaflets/pulp.jpg"
import pic9 from "../img/leaflets/square_spotuv.jpg"
import pic10 from "../img/leaflets/elegant.jpg"
import pic11 from "../img/leaflets/hot-stamping.jpg"
import ContactForm from "./ContactForm";


export default class Leaflets extends Component {
  constructor(props) {
    super(props);
    this.state = { modal: null };
    this.handleModal = this.handleModal.bind(this);
  }
  handleModal(e) {
    e.preventDefault();
    this.setState({ modal: e.target.style.backgroundImage.split('"')[1] })
  }
  render() {
    return (
      <div className="Leaflets">
        <div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-body" >
                <img className="modal-img" src={this.state.modal} alt={this.state.modal} />
              </div>
            </div>
          </div>
        </div>
        <div className="section-background">
          <h2>LEAFLETS &amp; FLYERS</h2>
        </div>
        <Info current="leaflets" />
        <div className="section-card-grid">
          <div>
            <h2>Leaflets &amp; Flyers</h2>
            <p>
              Leaflets and flyers a great way of advertising companies and they
              are still a very popular marketing tool.
            </p>
            <p>
              Our team can create a bespoke artwork tailored specifically to
              your brand.
            </p>
            <p>
              Leaflets are great when you promote small or large business, from
              restaurants up to industry.
            </p>
            <p>
              It only takes a glance to get someone's attention, make sure your
              brand will have that extra something to keep it!
            </p>
            <p>
              Advertised flyers, printed in full colour and designed for your
              company will build what necessary for your future.
            </p>
            <p>
              They are various useful sizes that are durable, easy to
              distribute, and perfect for gaining your brand some publicity.
            </p>
          </div>
          <ContactForm />
        </div>
        <h3>Our types of flyers and leaflets</h3>
        <div className="leaflets-grid">
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A7 Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A6 Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A5 Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A4 Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic1})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A3 Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic2})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>DL Flyers &amp; Leaflets</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic3})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Folded tri-fold</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic4})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Folded half-fold</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic4})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Folded Z-fold</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic5})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Squer</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic5})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>X-Long</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic5})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>A-Long</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Material</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Gloss</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Silk</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic6})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Perforated</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic7})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Laminated</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic8})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Pulp</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic9})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Spot UV</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic10})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Luxury</p>
          </div>
          <div className="product" onClick={this.handleModal} style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) ), url(${pic11})` }} data-toggle="modal" data-target="#exampleModalCenter">
            <p>Hot-stamping</p>
          </div>
        </div>
        <h3>Here are some of our sizes and shapes</h3>
        <div className="leaflets-sizes-grid">
          <div className="leaflet-size leaflet-size1" />
          <div className="leaflet-size leaflet-size2" />
          <div className="leaflet-size leaflet-size3" />
          <div className="leaflet-size leaflet-size4" />
          <div className="leaflet-size leaflet-size5" />
          <div className="leaflet-size leaflet-size6" />
        </div>
        <div className="lpf-input">
          <div>
            <h4>MAIL</h4>
            <input type="text" className="form-control" placeholder="Company mail" aria-label="email" aria-describedby="basic-addon1" />
          </div>
          <div>
            <h4>TITLE</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>PRODUCT</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>MATERIAL</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div className="file-input">
            <h4>GRAPHICS</h4>
            <label className="btn btn-primary">
              FRONT <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              BACK <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              TEXT FRONT <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              TEXT BACK <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              LOGO <input type="file" hidden />
            </label>
            <label className="btn btn-primary">
              OTHER <input type="file" hidden />
            </label>
          </div>
          <div>
            <h4>TEXT</h4>
            <input type="text" className="form-control" placeholder="Quantity" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>MAIL</h4>
            <input type="text" className="form-control" placeholder="Mail" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>PHONE</h4>
            <input type="text" className="form-control" placeholder="Phone" aria-describedby="basic-addon1" /></div>
        </div>
        <div className="fancy-input">
          <button
            type="button"
            className="btn btn-primary btn-circle"
          >
            SUBMIT
            </button>
        </div>
      </div>
    );
  }
}



