import React, { Component } from 'react'
import Info from "../components/Info";
import form from "../files/form.pdf"
import ContactForm from './ContactForm';

export default class FancyPapers extends Component {
  render() {
    return (
      <div className="FancyPapers">
        <div className="section-background">
          <h2>FANCY PAPERS</h2>
        </div>
        <Info current="fancy-papers" />
        <div className="section-card-grid">
          <div className="section-text" style={{ textAlign: 'left' }}>
            <p>
              We aim to support and develop services for business enterprises, projects, personal and private needs. This might be for small businesses, large companies or personal individual occasions and celebrations. Whichever service you are looking for we will tailor and offer solutions to provide for your requirements.
            </p>
            <p>
              Our printing house offers a bespoke service where we will create and design products to meet your original and individual design requirements. Our dedicated and friendly team will consult with you directly to establish precisely what you are looking for. We offer a variety of printing materials, different colours and paper shapes and sizes including A5, A3 and A4, printing fonts, texts, themes, imagery, ribbons, envelopes and much more. You can make your designs as unique as you like and make it one of a kind, be it luxurious, arnate, decorative and fanciful. Whatever you need, let us help you bring it to life!
            </p>
            <b>
              <p>
                Our experienced and professional graphic designers will consult with you to establish your needs. They will take your direction and also offer you advice to help you create and design your own unique designs and logos if needed.
              </p>
            </b>
          </div>
          <ContactForm />
        </div>
        <div className="section-text">
          <h2 className="section-header">PROFESSIONAL SERVICES</h2>
          <div className="fancy-papers-grid">
            <div className="fancy-papers-item professional-services-1"></div>
            <div className="fancy-papers-item professional-services-2"></div>
            <div className="fancy-papers-item professional-services-3"></div>
            <div className="fancy-papers-item professional-services-4"></div>
          </div>
          <p>
            Company papers such as business cards letterheads, folders, notebooks office paperwork such as             professional formes - e.g accident and incident forms, achievement certificates etc company advertising    folders, door hangers, direct mailings, signs, promotions and ads and many more.
          </p>
        </div>
        <div className="section-text">
          <h2 className="section-header">PRIVATE CUSTOMER SERVICES</h2>
          <div className="fancy-papers-grid">
            <div className="fancy-papers-item customer-services-1"></div>
            <div className="fancy-papers-item customer-services-2"></div>
            <div className="fancy-papers-item customer-services-3"></div>
            <div className="fancy-papers-item customer-services-4"></div>
          </div>
          <p>
            Greeting cards of any kind, themed postcards, signage of tables, guest books, thank you books, party invitations including arnate wedding invitations.
          </p>
        </div>
        <div className="section-text">
          <h2 className="section-header">SOMETHING FOR EVERYONE</h2>
          <div className="fancy-papers-grid">
            <div className="fancy-papers-item smth-for-everyone-1"></div>
            <div className="fancy-papers-item smth-for-everyone-2"></div>
            <div className="fancy-papers-item smth-for-everyone-3"></div>
            <div className="fancy-papers-item smth-for-everyone-4"></div>
          </div>
          <p>
            Advertising boards, placards, menus, flyers, ads, labels &amp; stickers, forms, cheques, prescriptions, branded merchandise wrapping paper, hang tags, gift cards, holders and coupons.
          </p>
        </div>
        <div className="section-text">
          <h2 className="section-header">SERVICES FOR SPORTS</h2>
          <div className="fancy-papers-grid">
            <div className="fancy-papers-item services-for-sports-1"></div>
            <div className="fancy-papers-item services-for-sports-2"></div>
            <div className="fancy-papers-item services-for-sports-3"></div>
            <div className="fancy-papers-item services-for-sports-4"></div>
          </div>
          <p>
            Starting numbers, sport colours, sport or random events tickets, professional sport shirts.
          </p>
          <b>
            <p>
              These are some examples of what we can offer. Full list is <a href={form} target="_blank">here</a> If you can't find the information you require please do not hesitate to contact us and we will be delighted to discuss and help you.
            </p>
          </b>
        </div>
        <h2 className="section-header">YOUR ORDER</h2>
        <div className="lpf-input">
          <div>
            <h4>MAIL</h4>
            <input type="text" className="form-control" placeholder="Company mail" aria-label="email" aria-describedby="basic-addon1" />
          </div>
          <div>
            <h4>TITLE</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div>
            <h4>TEXT</h4>
            <input type="text" className="form-control" placeholder="Title" aria-describedby="basic-addon1" /></div>
          <div className="file-input">
            <h4>FILE TO ADD</h4>
            <label className="btn btn-primary">
              GRAPHICS <input type="file" hidden />
            </label>
          </div>
          <div>
            <h4>QUANTITY</h4>
            <input type="text" className="form-control" placeholder="Quantity" aria-describedby="basic-addon1" /></div>
        </div>
        <div className="fancy-input">
          <button
            type="button"
            className="btn btn-primary btn-circle"
          >
            SUBMIT
            </button>
        </div>
      </div>
    )
  }
}



