import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "./App.css";
import Topbar from "./components/Topbar";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import Cookies from "./components/Cookies";
import Landing from "./components/Landing";
import Contact from "./components/Contact";
import Partnership from "./components/Partership";
import Products from "./components/Products";
import AboutSection from "./components/AboutSection";
import BusinessCards from "./components/BusinessCards";
import LargePrintFormat from "./components/LargePrintFormat";
import Leaflets from "./components/Leaflets";
import Canvas from "./components/Canvas";
import Specialist from "./components/Specialist";
import FancyPapers from "./components/FancyPapers";
import Calendars from "./components/Calendars";
import Printing from "./components/Printing";

class App extends Component {
  constructor(props) {
    super(props);
    this.handleLoad = this.handleLoad.bind(this);
  }
  componentDidMount() {
    window.addEventListener("load", this.handleLoad);
  }
  handleLoad() {
    document.getElementsByClassName("App")[0].style.opacity = 1;
  }
  render() {
    return (
      <Router>
        <div className="App">
          <Topbar />
          <Navbar />
          <Route exact path="/" component={Landing} />
          <Route exact path="/contact" component={Contact} />
          <Route exact path="/partnership" component={Partnership} />
          <Route exact path="/products" component={Products} />
          <Route exact path="/about" component={AboutSection} />
          <Route exact path="/business-cards" component={BusinessCards} />
          <Route exact path="/leaflets" component={Leaflets} />
          <Route exact path="/fancy-papers" component={FancyPapers} />
          <Route exact path="/specialist" component={Specialist} />
          <Route exact path="/canvas" component={Canvas} />
          <Route exact path="/calendars" component={Calendars} />
          <Route exact path="/printing" component={Printing} />
          <Route
            exact
            path="/large-print-format"
            component={LargePrintFormat}
          />
          <Footer />
          <Cookies />
        </div>
      </Router>
    );
  }
}

export default App;



