import React, { Component } from "react";

export default class componentName extends Component {
  render() {
    return (
      <div className="Back">
        <div className="container-back">
          <div className="box back-left">
            <div
              id="myCarousel"
              className="carousel slide"
              data-ride="carousel"
              data-interval="3000"
            >
              <ol className="carousel-indicators">
                <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                <li data-target="#myCarousel" data-slide-to="1"></li>
                <li data-target="#myCarousel" data-slide-to="2"></li>
                <li data-target="#myCarousel" data-slide-to="3"></li>
              </ol>
              <div className="carousel-inner">
                <div alt="First slide" className="carousel-item carousel-height active">
                  <h2><b>-&nbsp;
                    <div className="circle">10</div>
                    %
                      <br />
                    <span>DISCOUNT</span>
                    <br /> FOR PREPARATION
                      <br /> PROJECTS FOR PRINTING
                    </b>
                  </h2>
                  <hr />
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Exercitationem quia eaque quos nesciunt inventore dignissimos cum,
                    sit culpa blanditiis laboriosam!
                  </p>
                  <button
                    type="button"
                    className="btn btn-outline-primary btn-circle btn-read-more"
                  >Read more
                  </button>
                </div>
                <div className="carousel-item carousel-height">
                  <h2><b>PARTNERSHIP</b></h2>
                  <hr />
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Exercitationem quia eaque quos nesciunt inventore dignissimos cum,
                    sit culpa blanditiis laboriosam!
                  </p>
                  <a href="/partnership">
                    <button
                      type="button"
                      className="btn btn-outline-primary btn-circle btn-read-more"
                    >Read more
                 </button>
                  </a>
                </div>
                <div className="carousel-item carousel-height">
                  <h2><b> CONTACT</b></h2>
                  <hr />
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Exercitationem quia eaque quos nesciunt inventore dignissimos cum,
                    sit culpa blanditiis laboriosam!
                  </p>
                  <a href="/contact">
                    <button
                      type="button"
                      className="btn btn-outline-primary btn-circle btn-read-more"
                    >Read more
                  </button>
                  </a>
                </div>
                <div className="carousel-item carousel-height">
                  <h2><b>PORTFOLIO</b></h2>
                  <hr />
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Exercitationem quia eaque quos nesciunt inventore dignissimos cum,
                    sit culpa blanditiis laboriosam!
                  </p>
                  <a href="/portfolio">
                    <button
                      type="button"
                      className="btn btn-outline-primary btn-circle btn-read-more"
                    > Read more
                  </button>
                  </a>
                </div>
              </div>
            </div>

          </div>
          <div className="box back-right blank" />
        </div>
      </div>
    );
  }
}



