import React, { Component } from "react";
import logoonly from "../img/default/logo-only.png";
import Info from "../components/Info";

export default class Contact extends Component {
  render() {
    return (
      <div className="Contact">
        <section className="contact-us">
          <div className="contact-us-background">
            <h2>CONTACT</h2>
          </div>
          <Info />
          <div className="contact-us-address">
            <div className="contact-logo">
              <img src={logoonly} alt="Logo" />
              <h2>kiteprint</h2>
            </div>
            <p>
              A1 Dolphin Way | Shoreham by Sea | W Sx | BN43 6 NZ
              <br /> Telephone (12345) 67890
            </p>
            <button
              type="button"
              className="btn btn-outline-primary btn-circle"
            >
              SEND QUESTION
            </button>
          </div>
        </section>
      </div>
    );
  }
}



